import React, { useEffect, Suspense } from 'react';
import { useUserContext } from '../contexts/UserContext';
import { usePageVisibility } from "../hooks/usePageVisibility";
import { useNavigate } from "react-router-dom";
import ArtistInfo from "../components/artistInfo/Artist";
import TwinklingBackground from "../components/twinklingbackground/TwinklingBackground";
import HeroSection from "../components/hero/Hero";
import Navbar from "../components/navbar/Navbar";
import Footer from "../components/footer/Footer";
import Sidebar from "../components/sidebar/Sidebar";
// import axios from "axios";

const Thoughts = React.lazy(() => import("../components/thoughts/Thoughts"));
const Music = React.lazy(() => import("../components/music/Music"));
const NewSection = React.lazy(() => import("../components/new/NewSection"));
const CommentSection = React.lazy(() => import("../components/commentsection/CommentSection"));

const Home = () => {
  const { state, dispatch, setErrorMessage, clearErrorMessage, setSuccessMessage, clearSuccessMessage } = useUserContext();

  const navigate = useNavigate();
  const isVisible = usePageVisibility();

  const toggle = () => {
    dispatch({ type: "TOGGLE_SIDEBAR" });
  };

  useEffect(() => {
    async function fetchData() {
      try {
        const authResponse = await fetch("/api/auth/status", {
          credentials: "include",
        });
        if (!authResponse.ok) {
          throw new Error('Network response was not ok');
        }
        const authData = await authResponse.json();
        if (authData.isAuthenticated) {
          dispatch({ type: "SET_CURRENT_USER", payload: authData.user });
          setSuccessMessage("User authenticated successfully!");
          setTimeout(() => clearSuccessMessage(), 3000);
        }
      } catch (error) {
        console.error("Error checking auth status:", error);
        setErrorMessage("Error checking authentication status. Please try again later.");
        setTimeout(() => clearErrorMessage(), 3000);
      }
    }

    fetchData();
  }, [dispatch, setErrorMessage, clearErrorMessage, setSuccessMessage, clearSuccessMessage]);

  const handleSignOut = async () => {
    try {
      const response = await fetch(`${process.env.REACT_APP_BACKEND_URL}/auth/logout`, {
        method: "GET",
        credentials: "include",
      });
      if (response.ok) {
        dispatch({ type: "SET_CURRENT_USER", payload: null });
        navigate("/");
        setSuccessMessage("Successfully signed out.");
        setTimeout(() => clearSuccessMessage(), 3000);
      } else {
        throw new Error("Logout failed");
      }
    } catch (error) {
      console.error("Error during sign out:", error);
      setErrorMessage("Error during sign out. Please try again later.");
      setTimeout(() => clearErrorMessage(), 3000);
    }
  };

  const handleSignIn = () => {
    window.location.href = `${process.env.REACT_APP_BACKEND_URL}/auth/google`;
  };

  return (
    <>
      <TwinklingBackground />
      <Sidebar
        isOpen={state.isOpen}
        toggle={toggle}
        currentUser={state.currentUser}
        handleSignIn={handleSignIn}
        handleSignOut={handleSignOut}
      />
      <Navbar
        toggle={toggle}
        isOpen={state.isOpen}
        handleSignIn={handleSignIn}
        handleSignOut={handleSignOut}
        currentUser={state.currentUser}
        isVisible={isVisible}
      />
      <HeroSection />
      <ArtistInfo />
      <Suspense fallback={<div>Loading Music...</div>}>
        <Music />
      </Suspense>
      <Suspense fallback={<div>Loading New Section...</div>}>
        <NewSection />
      </Suspense>
      <Suspense fallback={<div>Loading Thoughts...</div>}>
        <Thoughts />
      </Suspense>
      <Suspense fallback={<div>Loading Comments...</div>}>
        <CommentSection currentUser={state.currentUser} dispatch={dispatch} />
      </Suspense>
      {state.message && <div className="message">{state.message}</div>}
      {state.errorMessage && <div className="error-message">{state.errorMessage}</div>} {/* Display error message */}
      <Footer />
    </>
  );
};

export default Home;
